import React, { useState } from 'react';
import {Button, Flex, FlexItem, GridItem, Text, TextInput, Title} from "@patternfly/react-core";
import EditIcon from "@patternfly/react-icons/dist/esm/icons/edit-icon";
import AcceptIcon from "@patternfly/react-icons/dist/esm/icons/check-icon";
import CancelIcon from "@patternfly/react-icons/dist/esm/icons/times-icon";
import PropTypes from "prop-types";

const IDPField = (props) => {

    // handles if the use has chosen to edit this field. toggles between the two GridItem's
    const [editField, toggleEditField] = useState(false);
    // handles the text input changes
    const [textValue, updateTextValue] = useState("");

    /**
     * called by the check button, it needs to submit the data change and re-trigger the toggle state.
     */
    const handleAcceptClick = () => {
        console.log("triggered submit value: " + textValue);
        toggleEditField(false)
    }

    return (<div>
                <GridItem hidden={editField}>
                    <Title headingLevel="h4">{props.title}
                        <Button
                            variant="link"
                            onClick={() => { toggleEditField(true) } }
                            isInline
                            icon={<EditIcon />}
                            iconPosition="right"
                            className="pf-u-ml-xs  editIcon">

                        </Button>
                    </Title>
                    <Text id={props.idValue + "-current"}>{props.current}</Text>
                </GridItem>
                <GridItem hidden={!editField}>
                    <Title headingLevel="h4">{props.title}</Title>
                    <Flex>
                        <FlexItem><TextInput id={props.idValue} value={textValue} onChange={(update) => { updateTextValue(update) } }></TextInput></FlexItem>
                        <FlexItem><Button variant="link" onClick={handleAcceptClick} isInline icon={<AcceptIcon />} iconPosition="right" className="pf-u-ml-xs  editIcon"></Button></FlexItem>
                        <FlexItem>
                            <Button
                                variant="link"
                                onClick={() => { toggleEditField(false) }}
                                isInline
                                icon={<CancelIcon />}
                                iconPosition="right"
                                className="pf-u-ml-xs  editIcon">

                            </Button>
                        </FlexItem>
                    </Flex>
                </GridItem>
            </div>
    )
}

IDPField.propTypes = {
    title: PropTypes.string,
    idValue: PropTypes.string,
    current: PropTypes.string,
}

export default IDPField;
