// step two, choice 1 of the flow https://miro.com/app/board/uXjVO_x__QE=/
import React, {useState} from 'react';
import { 
    Text,
    TextInput,
    Radio,
    Form,
    FormGroup
} from '@patternfly/react-core';
import {processAllInputs, setStateValue} from '../../utils/general-utils.js';
import PropTypes from 'prop-types';

const OIDCConfiguration = (props) => {

    const [selections, setSelections] = useState();
    const [validations, setValidations] = useState({
        'clientId': {
            inputType: 'GENERAL',
            status: 'default',
            message: ''
        },
        'clientSecret': {
            inputType: 'GENERAL',
            status: 'default',
            message: ''
        },
        'inputMethod': {
            inputType: 'RADIO',
            status: 'default',
            message: ''
        }
    });


    /**
     * handle selection needs to update information being used to govern
     * the applicaton flow as well as the final api update object
     */
    const handleSelection = (value) => {

        /*
         * regardless of current state. if the user alters the input clear existing flags. This should also reset
         * the currently displayed errors
         */
        props.resetNextClicked(false);

        //  update the main data storage object with the new value
        props.rootUserChoices.current[value.target.id] = value.target.value;

        /*
         * update the validation settings. It is expected that the id's used in the validation object matches
         * the id's used on the elements.
         */
        const failureTriggered = processAllInputs(props.rootUserChoices.current, validations, setValidations);

        props.rootValidation.current.OIDCCONFIG = !failureTriggered;

        if (!failureTriggered && props.rootUserChoices.current.inputMethod) {
            props.updateStatus(props.rootUserChoices.current.inputMethod);
        }

    }

    return <Form>
                <Text className="pf-u-color-400 pf-u-font-size-sm">All fields are required</Text>
                <FormGroup
                    label="Client ID/SP entity ID"
                    helperText="The Red Hat client identifier registered within the customer identity provider. Your indentity provider generates this client ID when a Red Hat client is added"
                    helperTextInvalid={validations.clientId.message}
                    validated={props.nextClicked ? validations.clientId.status : ''}
                    onChange={(value) => { handleSelection(value) }}
                >
                    <TextInput
                        onChange={(value) => setStateValue(value, "clientId", selections, setSelections)}
                        type="text"
                        id="clientId"
                    />
                </FormGroup>

                <FormGroup
                    label="Client Secret"
                    helperText="Enter the client secret associated with your client identifier  by your identity provider"
                    helperTextInvalid={validations.clientSecret.message}
                    validated={props.nextClicked ? validations.clientSecret.status : ""}
                    onChange={(value) => { handleSelection(value) }}>
                    <TextInput
                        onChange={(value) => setStateValue(value, "clientSecret", selections, setSelections)}
                        type="text"
                        id="clientSecret"
                    />
                </FormGroup>

                <FormGroup
                    onChange={(value) => { handleSelection(value) }}
                    label="Load external IDP metadata from a config file or download it from a url"
                    helperTextInvalid={"You must choose a method for importing your metadata"}
                    validated={props.nextClicked ? validations.inputMethod.status : ""}
                >
                    <Radio name="inputChoice" id="inputMethod" label="Import from URL" value="IMPORTFROMURL"/>
                    <Radio name="inputChoice" id="inputMethod" label="Import from file" value="IMPORTFROMFILE"/>
                </FormGroup>

                
            </Form>

}

OIDCConfiguration.propTypes = {
    resetNextClicked: PropTypes.func,
    rootUserChoices: PropTypes.object,
    rootValidation: PropTypes.object,
    nextClicked: PropTypes.func,
    updateStatus: PropTypes.func
}

export default OIDCConfiguration;
